"use client";

import { useState } from "react";
import { Menu } from "lucide-react";
import { Sheet,SheetTrigger } from "./ui/sheet";
import {DropdownMenu} from "./ui/dropdown";

interface HeaderProps {
  user: {
    firstname: string;
    lastname: string;
  } | null;
  logout: () => void;
}


export default function Header({user, logout}:HeaderProps) {
  const [open, setOpen] = useState(false);
  const menuElements = [
    { title: "Profile", onClick: () => console.log("Profile") },
    { title: "Logout", onClick: logout },
  ];

  return (
    <header>
      <div className="bg-black px-4 md:px-6 w-full h-[4rem]">
        <Sheet open={open} onOpenChange={setOpen}>
            <SheetTrigger asChild>
                <button 
                className="text-white sm:hidden"
                onClick={()=>setOpen(!open)}
                >
                <Menu/>    
                </button>
            </SheetTrigger>
        </Sheet>
        {/* <DropdownMenu
        trigger={
            <div>
                <p className="text-white text-right text-md pb-6 md:p-5">{user ?`${user?.firstname} ${user.lastname}`:""}</p>
            </div>
        }
        menuElements={menuElements}
        >

        </DropdownMenu>
         */}
        
      </div>

    </header>
  );
}
